import React from "react";
import { Grid, Text, Button, Input, Image } from "../elements";
import Upload from "../shared/Upload";

import { useSelector, useDispatch } from "react-redux";
import { useState, useRef } from "react";
//addpostFB   import
import { actionCreators as postActions } from "../redux/modules/post";
import { actionCreators as imageActions } from "../redux/modules/image";



const PostWrite = (props) => {
    const dispatch = useDispatch();
    const in_login = useSelector((state) => state.user.is_login);

    const post_list = useSelector((state) => state.post.list);
    //   preview  
    const preview = useSelector((state) => state.image.preview);
    const { history } = props;

    const post_id = props.match.params.id;
    const is_edit = post_id ? true : false;

    //  : post_id   id 
    let _post = is_edit ? post_list.find((p) => p.id === post_id) : null;

    //post_id     
    React.useEffect(() => {
        if (is_edit && !_post) {
            window.alert('  !');
            console.log('  !');
            history.goBack();

            return;
        }
        if (is_edit) {
            dispatch(imageActions.setPreview(_post.image_url));
        }
        if (!is_edit) {
            dispatch(imageActions.setPreview(null));
        }

    }, []);



    const [contents, setContents] = React.useState(_post ? _post.contents : "");
    const [layout, setLayout] = useState("center");



    const changeContents = (e) => {
        setContents(e.target.value);
    }

    const changeLayout = (e) => {
        setLayout(e.target.value)
    }


    //   addPostFB contents 
    const addPost = () => {
        dispatch(postActions.addPostFB(contents, layout))
        window.alert(" :)")
    }

    const editPost = () => {
        dispatch(postActions.editPostFB(post_id, { contents: contents, layout: layout }))

        window.alert(" :)")
    }

    if (!in_login) {
        return (
            <Grid margin="100px 0" padding="16px" center>
                <Text size="32px" bold >! !</Text>
                <Text size="16px">     !</Text>
                <Button _onClick={() => { history.replace("/") }}>  </Button>
            </Grid>
        )
    }

    return (
        <React.Fragment>
            <Grid padding="16px">
                <Text size="36px" bold>{is_edit ? " " : " "}</Text>
                <Upload />
            </Grid>

            <Grid>
                <Grid padding="16px">
                    <Text size="24px" margin="0px" bold></Text>
                </Grid>
            </Grid>

            <Grid margin="3em 0;">
                <Text size="1.5em;" margin="20px 0px" bold> </Text>
                <input
                    name="layout"
                    type="radio"
                    value="right"
                    onChange={changeLayout}
                    defaultChecked={setLayout === "right" ? true : false}
                /> 
                <Grid is_flex margin="1em 0 2em 0;">
                    <Text width="50%;" center>{contents}</Text>
                    <Grid>
                        <Image shape="rectangle" src={preview ? preview : 'https://camfitt.com/static/media/empty.a5238aed.jpg'} />
                    </Grid>
                </Grid>
                <input
                    name="layout"
                    type="radio"
                    value="left"
                    onChange={changeLayout}
                    defaultChecked={setLayout === "left" ? true : false} />
                
                <Grid is_flex margin="1em 0 2em 0;">
                    <Grid>
                        <Image shape="rectangle" src={preview ? preview : 'https://camfitt.com/static/media/empty.a5238aed.jpg'} />
                    </Grid>
                    <Text width="50%" center>{contents}</Text>
                </Grid>
                <input
                    name="layout"
                    type="radio"
                    value="center"
                    onChange={changeLayout}
                    defaultChecked={setLayout === "center" ? true : false}
                /> 
                <Grid margin="1em 0 2em 0;">
                    <Text margin=" 2em 0;">{contents}</Text>
                    <Image shape="rectangle" src={preview ? preview : 'https://camfitt.com/static/media/empty.a5238aed.jpg'} />
                </Grid>
            </Grid>


            <Grid padding="16px">
                <Input
                    value={contents}
                    bold
                    _onChange={changeContents}
                    label=" "
                    placeholder=" "
                    multiLine />
            </Grid>

            <Grid padding="16px">
                {is_edit ? (
                    <Button text=" " bold _onClick={editPost}></Button>
                ) : (
                    <Button text=" " bold _onClick={addPost}></Button>
                )}
            </Grid>


        </React.Fragment>
    )
}

export default PostWrite;

